<template>
  <div class="loginarea">
    <div class="login_text">
        <span>로그인</span>
    </div>
    <div class="logininput">
      <input type="text" placeholder="아이디" id="userid" v-model="userid"><br>
      <input type="password" placeholder="비밀번호" id="userpw" v-model="userpw">
   </div>
   <div class="loginbtn">
      <button type="button" class="btn btn-primary" @click="login()">로그인</button>
      <button type="button" class="btn btn-secondary">뒤로가기</button>
   </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'MainPage',
  data(){
    return{
        userid:'',
        userpw:''
    }
  },
  methods:{
    login(){

        var url = 'http://localhost:8080/login';
        axios.get(url,{
        params: {
            id : this.userid,
            pw : this.userpw
        }
    })
    .then(response => {

        if(response.data == "success"){
          console.log("로그인 성공");
          this.$router.push({ path: "/main" });
        }else if (response.data == "nomember") {
          console.log("없는 회원");
        }else if (response.data == "differentpw"){
          console.log("비밀번호가 다름");
        }
    })
    .catch(error => {
        console.log(error);
    })
  }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .login_text{
        height: 150px;
        display: block;
        margin: 0 auto;
        margin-top: 50px;
        text-align: center;
    }
    .login_text span{
        font-size: 30px;
        font-weight: bold;
        color: #002b36;
    }
    .logininput input{
        width: 245px;
        margin-bottom: 5px;
    }
    .loginbtn button{
        width: 120px;
        margin-right: 5px;
        margin-bottom: 262px;
    }
</style>
